import AbstractEye from './AbstractEye';
import DOF from "./DOF";
import {conversion} from 'jibo-keyframes';
import DOFValues from "./DOFValues";


class Eye extends AbstractEye {
    update(timestamp:Array<number>, dofValues:DOFValues)  {
        if (!this.connected) {
            return;
        }
        let vertices:Array<DOF> = this.eyeMesh.dofs;

        if(dofValues.eyeTextureInfixBn_r !== this.texturePrev) {
            this.texturePrev = dofValues.eyeTextureInfixBn_r;
            this.texture = this.texturePrev;
        }

        vertices[0].x = conversion.toPixelsX(dofValues.vertexJoint1_t);
        vertices[0].y = conversion.toPixelsY(dofValues.vertexJoint1_t_2);

        vertices[1].x = conversion.toPixelsX(dofValues.vertexJoint2_t);
        vertices[1].y = conversion.toPixelsY(dofValues.vertexJoint2_t_2);

        vertices[2].x = conversion.toPixelsX(dofValues.vertexJoint3_t);
        vertices[2].y = conversion.toPixelsY(dofValues.vertexJoint3_t_2);

        vertices[3].x = conversion.toPixelsX(dofValues.vertexJoint4_t);
        vertices[3].y = conversion.toPixelsY(dofValues.vertexJoint4_t_2);

        vertices[4].x = conversion.toPixelsX(dofValues.vertexJoint5_t);
        vertices[4].y = conversion.toPixelsY(dofValues.vertexJoint5_t_2);

        vertices[5].x = conversion.toPixelsX(dofValues.vertexJoint6_t);
        vertices[5].y = conversion.toPixelsY(dofValues.vertexJoint6_t_2);

        vertices[6].x = conversion.toPixelsX(dofValues.vertexJoint7_t);
        vertices[6].y = conversion.toPixelsY(dofValues.vertexJoint7_t_2);

        vertices[7].x = conversion.toPixelsX(dofValues.vertexJoint8_t);
        vertices[7].y = conversion.toPixelsY(dofValues.vertexJoint8_t_2);

        vertices[8].x = conversion.toPixelsX(dofValues.vertexJoint9_t);
        vertices[8].y = conversion.toPixelsY(dofValues.vertexJoint9_t_2);

        this.x = conversion.toPixelsX(dofValues.eyeSubRootBn_t);
        this.y = -conversion.toPixelsX(dofValues.eyeSubRootBn_t_2);

        this.red = dofValues.eye_redChannelBn_r;
        this.green = dofValues.eye_greenChannelBn_r;
        this.blue = dofValues.eye_blueChannelBn_r;

        this.rotation = -dofValues.eyeSubRootBn_r;
    }

}
export default Eye;